<template>
<div class="Message-box">
    <Header class="header">
        <van-icon  tag="router-link" to='/' slot="first" name="arrow-left" info="" />
        <p slot="mid">我的</p>
        <p slot="last"></p>
    </Header>
    <div class="Message-box-one">
        <!-- 第一个子元素 -->
        <div>   
            <router-link tag="section" to='/my_message' class="message-Id">
                <div><img :src="this.userImg" alt=""/></div>
                <div>
                    <p><b>{{this.userName}}</b></p>
                    <p>
                        <span class="el-icon-mobile-phone"></span>
                        
                        <span v-if="this.$store.state.log==''">暂无绑定手机号</span><span v-if="this.$store.state.log!=''">绑定的手机号</span>
                    </p>
                </div>
                <van-icon tag="div" name="arrow" color="#fff" size='0.37rem' />
            </router-link>

            <section class="message-price">
                <ul>
                    <li>
                        <p><span><b>0.00</b></span>元</p>
                        <p>我的余额</p>
                    </li>
                    <li>
                        <p><span><b>3</b></span>个</p>
                        <p>我的优惠</p>
                    </li>
                    <li>
                        <p><span><b>0</b></span>分</p>
                        <p>我的积分</p>
                    </li>
                </ul>
            </section>
            <div class="Mesg_aDiv-Label">
                <ul>
                    <router-link tag='li' to='/order' v-for='(item,index) in List' :key="index">
                        <p :class="item.ClassNmae" :style="{color:item.clolr}">
                            <span>{{item.names}}</span>
                        </p>
                        
                        <van-icon tag="p" name='arrow' />
                    </router-link>
                </ul>
                <ul>
                    <router-link tag='li' to='/' v-for='(item,index) in Arr' :key="index">
                        <p :class="item.ClassNmae" :style="{color:item.clolr}">
                        <span>{{item.names}}</span>
                        </p>
                        <van-icon tag="p" name='arrow' />
                    </router-link>
                </ul>
            </div>

        </div>
    </div>
</div>
</template>

<script>
import Header from '@/components/header'
/***利用event对象target方法中classList属性实现点击哪个就给哪个添加颜色并且让搜索框和点击的数据同步
1.定义初始化数据iN=-1,2.给要点击的数据添加:class="{ac:iN==index}"
3.给绑定的事件添加的方法add()中传入e和下标连个参数
add(e,index){
    if(e.target.classList=="ac"){
           this.iN=-1;
    }else{
        this.iN=index;
        this.txt=e.target.innerHTML
    }
},*/
	 export default {
        name: "Message",
        components:{
            Header
        },
        data(){
            return{
                userName:'',
                userImg:'',
                List:[
                    {ClassNmae:'iconfont icon--',names:'我的订单',path:'/order',clolr:''},
                    {ClassNmae:'iconfont icon-shangchengjifenshangcheng_xuanzhong',names:'积分商城',path:'/',clolr:'rgb(252, 123, 83)'},
                    {ClassNmae:'iconfont icon-huangguanwangguanhuiyuanshenfendengjirenzhengxianxing',names:'饿了么会员卡',path:'/',clolr:'rgb(255, 198, 54)'}
                ],
                Arr:[
                    {ClassNmae:'iconfont icon-fuwuzhongxin',names:'服务中心',path:'/',clolr:'#6ac20b'},
                    {ClassNmae:'iconfont icon-xiazaiAPP',names:'下载饿了么APP',path:'/',clolr:'#3399ff'},
                ],
                bsys:false,
            }
        },
        methods: {
        },
        mounted() {
            var id=this.$store.state.log.id
            // console.log(id)
            this.axios.get(`http://elm.cangdu.org/v1/user?user_id=${id}`).then((res)=>{
                // console.log(res)
                this.userName=res.data.username;
            });
            this.axios.get(`http://elm.cangdu.org/img/default.jpg`).then((res)=>{
                console.log(res)
                this.userImg=res.config.url;
            })
            if(localStorage.log){
                this.bsys=true;
            }else{
                this.bsys=false;
            }

            new IScroll('.Message-box-one',{})
        }
    }
</script>

<style lang='scss'>
a{
    text-decoration: none;
}
ul,li,p{margin: 0;padding: 0;}
ul{
    list-style: none;
    li{list-style: none;}
} 
.header{
//     position: fixed;
//     top: 0;
//   z-index: 100;
>a{color: #fff;}

}

.Message-box{
    height: 100%;width: 100%;
    background: #ededed;
    display: flex;
    flex-direction: column;
    .Message-box-one{
        flex: 1;
        overflow: auto;
    }
}
.message-Id{
    height: 1.48rem;
    background: #3190e8;
    div:nth-of-type(1){
        width: 0.96rem;height: 0.96rem;
        overflow: hidden;
        background: #ededed;float: left;
        border-radius: 50%;
        color: rgb(126, 118, 118);
        margin-top: 0.26rem; margin-left: 0.23rem;
        img{width: 100%;height: 100%;}
    }
    div:nth-of-type(2){
        color: #fff;float: left;
        font-size: 0.3rem;
        margin-top: 0.36rem;margin-left: 0.18rem;
    }
    div:nth-of-type(3){
        float: right;margin-right:0.16rem;
        line-height: 1.48rem;
    }
}
.message-price{
    height: 1.4rem;background:#fff;
    ul{
        display: flex;
        li{
            box-sizing: border-box;
            border-right: 0.008rem solid #ededed;
            height: 1.4rem;width: 33.333%;
            text-align: center;
            p{
                font-size: 0.26rem;color:#666;
                span{
                    font-size: 0.5rem;
                    b{
                        display: inline-block;
                        color: #f90;
                        font-weight: 700;
                        line-height: 1rem;
                        font-family: Helvetica Neue,Tahoma;
                    }
                }
            }
        }
        li:nth-of-type(2){
            p:nth-of-type(1){
                span{
                    b{color: #ff5f3e;}
                }
            }
        }
        li:nth-of-type(3){
            p:nth-of-type(1){
                span{b{color: #6ac20b;}}
            }
        }
    }
}
.Mesg_aDiv-Label{
    width: 100%;
    ul{
        width: 100%;margin-top: 0.2rem;
        background: white;
        li{
            padding: 0.16rem 0.2rem;
            display: flex;
            justify-content: space-between;
            align-items:center;
            font-size: 0.25rem;
            border-bottom: 1px solid #f1f1f1;
            p{
                font-size: 0.25rem;
                >span{color: #000;padding-left:0.06rem; }
            }
            p:nth-of-type(2){color: rgb(187, 187, 187);}
        }
    }
}







</style>


